import axios, { AxiosResponse } from 'axios';

// Define the Dog interface
export interface Dog {
  id: string;
  img: string;
  name: string;
  age: number;
  zip_code: string;
  breed: string;
  city?: string;
  state?: string;
}

// Define the LoginData interface
interface LoginData {
  name: string;
  email: string;
}

// Define the QueryParams interface
interface QueryParams {
  size: number;
  sort: string;
  from?: number;
  next?: string;
  breeds?: string[] | null;
  ageMin?: number;
  ageMax?: number;
  zipCodes?: string[] | null;
}

// Define the Search interface
interface Search {
  next: string;
  resultIds: string[];
  total: number;
}

// Define the Match interface
interface Match {
  match: string;
}

// Define the location interface
export interface Location {
  zip_code: string;
  latitude: number;
  longitude: number;
  city: string;
  state: string;
  county: string;
}

interface LocationSearchParams {
  city?: string;
  states?: string[];
  size?: number;
  from?: number;
}

interface LocationsSearchResponse {
  results: Location[];
  total: number;
}

// Hide this in env later
const baseURL = process.env.REACT_APP_BASE_URL;

// Define the axios interceptor to catch 401s for re-auth
const apiInstance = axios.create({
  baseURL: baseURL,
  withCredentials: true,
});

apiInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  async (error) => {
    const originalRequest = error.config;
    if (error.response.status === 401 && !originalRequest._retry) {
      originalRequest._retry = true;
      const authData = JSON.parse(localStorage.getItem('authData') || 'null');
      if (authData) {
        const res = await APIService.authenticate(authData);
        if (res.status === 200) {
          console.log('re-auth success:', res.status);
          return apiInstance(originalRequest);
        }
      }
    }
    return Promise.reject(error);
  },
);

// Define the API service
const APIService = {
  authenticate: async (data: LoginData): Promise<AxiosResponse<any>> => {
    try {
      localStorage.setItem('authData', JSON.stringify(data));
      const response = await apiInstance.post(`/auth/login`, data);
      console.log(`Login successful. Welcome, ${data.name}!`);
      return response;
    } catch (error) {
      console.error('An error occurred while making the request: ', error);
      throw error;
    }
  },

  getDogsIds: async (params: QueryParams): Promise<AxiosResponse<Search>> => {
    try {
      const response = await apiInstance.get(`/dogs/search`, { params });
      // console.log('getDogsIds response:', response.status);
      return response;
    } catch (error) {
      console.log(error);
      throw error;
    }
  },

  getDogs: async (data: string[]): Promise<AxiosResponse<Dog[]>> => {
    try {
      const response = await apiInstance.post(`/dogs`, data, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      // console.log('getDogs status:', response.status);
      return response;
    } catch (error) {
      console.log(error);
      throw error;
    }
  },

  logout: async (): Promise<void> => {
    try {
      // Clear the user's login data from local storage
      localStorage.removeItem('authData');

      // Make the logout API call
      await apiInstance.post('/auth/logout');
      console.log('Logout successful');
    } catch (error) {
      console.error('An error occurred while logging out: ', error);
      throw error;
    }
  },

  match: async (data: string[]): Promise<AxiosResponse<Match>> => {
    try {
      const response = await apiInstance.post(`/dogs/match`, data, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      // console.log('match:', response.data, response.status);
      return response;
    } catch (error) {
      console.log(error);
      throw error;
    }
  },

  fetchLocations: async (
    zipCodes: string[],
  ): Promise<AxiosResponse<Location[]>> => {
    try {
      const response = await apiInstance.post(`/locations`, zipCodes, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      // console.log('fetchLocations status:', response.status);
      return response;
    } catch (error) {
      console.log('Error fetching locations:', error);
      throw error;
    }
  },

  // New method for fetching locations by city or state
  fetchLocationsByCityOrState: async (
    params: LocationSearchParams,
  ): Promise<AxiosResponse<LocationsSearchResponse>> => {
    try {
      const response = await apiInstance.post(`/locations/search`, params, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
      // console.log('fetchLocationsByCityOrState status:', response.status);
      return response;
    } catch (error) {
      console.log('Error fetching locations by city or state:', error);
      throw error;
    }
  },
};

export default APIService;
